#main-header #main-nav {
  float: right;
}

#main-header #main-nav ul li {
  list-style: none;
}

#main-header #main-nav ul li a.normal::after {
  content: '';
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 3px;
  background-color: #202027;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: .2s all ease-in-out;
  transition: .2s all ease-in-out;
}

.delete-act {
  color: #cc5555;
}

#main-header #main-nav ul li a.normal:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transition: .2s all ease-in-out;
  transition: .2s all ease-in-out;
}

#main-header #main-nav .hamburger {
  display: none;
  top: 20px;
  right: 20px;
  z-index: 4;
}

@media (max-width:900px) {
  #main-header #main-nav .hamburger {
    display: block;
  }

  #main-header #main-nav ul li a.normal {
    display: none;
  }
}

#main-header #main-nav .hamburger .slice {
  width: 30px;
  background-color: #202027;
  left: 0;
  top: 0;
  margin: 5px 0;
  padding: 2px;
  cursor: pointer;
  -webkit-transition: .5s all ease-in-out;
  transition: .5s all ease-in-out;
}

#main-header #main-nav .hamburger.cross .slice {
  background-color: #ffffff93;
  -webkit-transition: .5s all ease-in-out;
  transition: .5s all ease-in-out;
}

#main-header #main-nav .hamburger:hover.cross .slice {
  background-color: #fff;
}

#main-header #main-nav .hamburger.cross .slice:nth-child(1) {
  top: 10px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

#main-header #main-nav .hamburger.cross .slice:nth-child(2) {
  left: -10px;
  opacity: 0;
}

#main-header #main-nav .hamburger.cross .slice:nth-child(3) {
  top: -8px;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}
